/**
 * WordPress Dependencies
 */
import React from 'react';
import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';

/**
 * Custom Dependencies
 */
import {
	BorderControl,
	ColorSetting,
	ColorSettingWithGradient,
	DimensionControl,
} from '../../components';

function Inspector( props ) {
	return (
		<>
			<InspectorControls group="color">
				<ColorSetting
					attrKey={ 'contentsTextColor' }
					label={ __( 'Contents Text Color', 'gutenberghub-tabs' ) }
				/>
				<ColorSettingWithGradient
					attrBackgroundKey={ 'contentsBackgroundColor' }
					attrGradientKey={ 'contentsGradientBackground' }
					label={ __( 'Contents Background', 'gutenberghub-tabs' ) }
				/>
			</InspectorControls>
			<InspectorControls group="dimensions">
				<DimensionControl
					paddingLabel={ __(
						'Contents Padding',
						'gutenberghub-tabs'
					) }
					label={ __( 'Contents Padding', 'gutenberghub-tabs' ) }
					paddingAttributeKey="contentsPadding"
				/>
			</InspectorControls>
			<InspectorControls group="border">
				<BorderControl
					attrBorderKey="contentsBorder"
					attrBorderRadiusKey="contentsBorderRadius"
					borderLabel={ __( 'Contents Border', 'gutenberghub-tabs' ) }
					borderRadiusLabel={ __(
						'Contents Border Radius',
						'gutenberghub-tabs'
					) }
				/>
			</InspectorControls>
		</>
	);
}

export default Inspector;
