/**
 * WordPress Dependencies
 */
import { isEmpty } from 'lodash';
import { __ } from '@wordpress/i18n';
import {
	useBlockEditContext,
	__experimentalSpacingSizesControl as SpacingSizesControl,
} from '@wordpress/block-editor';
import { useSelect, useDispatch } from '@wordpress/data';
import { __experimentalToolsPanelItem as ToolsPanelItem } from '@wordpress/components';

function DimensionControl( {
	paddingLabel,
	paddingAttributeKey,
	showDefaultPadding = false,
} ) {
	const { clientId } = useBlockEditContext();
	const attributes = useSelect(
		( select ) =>
			select( 'core/block-editor' ).getSelectedBlock().attributes
	);
	const { updateBlockAttributes } = useDispatch( 'core/block-editor' );
	const setAttributes = ( newAttributes ) => {
		updateBlockAttributes( clientId, newAttributes );
	};
	return (
		<>
			<ToolsPanelItem
				panelId={ clientId }
				isShownByDefault={ showDefaultPadding }
				resetAllFilter={ () => {
					setAttributes( {
						[ paddingAttributeKey ]: {},
					} );
				} }
				className={ 'tools-panel-item-spacing' }
				label={ paddingLabel }
				onDeselect={ () =>
					setAttributes( { [ paddingAttributeKey ]: {} } )
				}
				hasValue={ () =>
					! isEmpty( attributes[ paddingAttributeKey ] )
				}
			>
				<SpacingSizesControl
					allowReset={ true }
					label={ paddingLabel }
					values={ attributes[ paddingAttributeKey ] }
					sides={ [ 'top', 'right', 'bottom', 'left' ] }
					onChange={ ( newValue ) => {
						setAttributes( {
							[ paddingAttributeKey ]: newValue,
						} );
					} }
				/>
			</ToolsPanelItem>
		</>
	);
}

export default DimensionControl;
